.container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: #889cae;
}

.calculator {
  padding: 20px;
  background-color: aliceblue;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.btn {
  height: 50px;
  width: 50px;
  margin: 4px;
  font-weight: bold;
  border-radius: 50px;
  border: none;
  box-shadow: 3px 3px #b1aeae;
}

.btn:hover {
  background-color: #889cae;
}
.inputBox {
  width: 74%;
  text-align: right;
  padding: 16px;
  height: 20%;
  margin-left: 3px;
  font-size: 17px;
  box-shadow: 2px 2px 5px #b1aeae;
  margin-bottom: 7px;
  border: none;
}
.equal {
  width: 105px;
}
